const GLOBAL_CONFIG_NAME = 'htmlplus';

const globalConfig = (instance, namespace) => {
  const config = (window[GLOBAL_CONFIG_NAME] || {})[namespace] || {};
  Object.keys(config)
    .map((key) => {
    const value = config[key];
    if (typeof value == 'undefined')
      return;
    instance[key] = value;
  });
};

export { globalConfig as g };
